@tailwind base;
@tailwind components;
@tailwind utilities;


@layer components {
  .max-container {
    max-width: 1440px;
    margin: 0 auto;
  }

  .padding{
    @apply px-[2rem] sm:px-[3rem] py-4 md:px-[5rem] sm:py-4
  }

  .round-buttons{
    @apply bg-customRed py-4 px-7 rounded-3xl text-white
  }

  .titles{
    @apply text-4xl font-extrabold mb-14 text-textColor
  }

  .category-cards{ /*10 rem for padding , 1.25 for each gap. query uses the point or greater. md means effect applies for 768 or higher*/
    @apply  h-[19rem]  2xl:min-w-[calc((100%-11.5rem-5rem)/5)]
    xl:min-w-[calc((100%-11.5rem-3.75rem)/4)]
    md:min-w-[calc((100%-11.5rem-2.5rem)/3)]
    sm:min-w-[calc((100%-11.5rem-1.25rem)/2)]
    min-w-[calc(100%-11.5rem)]
     text-center p-2 bg-white rounded-3xl flex flex-col items-center justify-evenly flex-shrink-0 shadow-lg shadow-slate-400
  
  }

  .recipe-smalltitle{
    @apply text-lightColor font-bold text-lg mb-6
  }

  .scroll-button{
    @apply w-16 absolute top-0 h-full text-5xl text-lightColor
  }

  .hover-cards{
    @apply text-white bg-customRed transition-all
  }
  
}



.navigation > nav > a{
  display:flex;
  align-items: center;
  column-gap: 4px;
}

img{
  width:100%;
  height:100%;
  object-fit:contain;
}

.recipe-icons  span{
  display: flex;
  flex-direction: row;
  align-items: center;
  column-gap: 0.7rem;
  background-color: #F54748 ;
  padding:0.4rem 1rem;
  border-radius: 3rem;
  font-weight: bold;
 
}

.recipe-icons svg{
  background-color: white;
  border-radius: 100%;
  padding:4px;
  font-size: 1.5rem;
}

.recipe-icons p{
  color:white;
}

.hide-scrollbar::-webkit-scrollbar {
  display: none; 
}







